@font-face {
  font-family: 'TT Travels';
  src: url(../fonts/TTTravels-DemiBold.eot);
  src: url(../fonts/TTTravels-DemiBold.eot?#iefix) format('embedded-opentype'),
       url(../fonts/TTTravels-DemiBold.otf) format('otf'),
       url(../fonts/TTTravels-DemiBold.ttf) format('truetype'),
       url(../fonts/TTTravels-DemiBold.svg#TTTravels-DemiBold) format('svg'),
       url(../fonts/TTTravels-DemiBold.woff) format('woff'),
       url(../fonts/TTTravels-DemiBold.woff2) format('woff2');
  font-weight: 600;
  font-style: normal;
}

